.event-types label {
  display: block;
}
#paid_select label {
  display: block;
  font-weight: normal;
}
#paid_select label span {
  cursor: pointer;
}
#paid_select label span.selected {
  font-weight: bold;
}
#paid_select .text-left {
  padding-right: 0;
}
#paid_select .text-center {
  padding: 0;
}
#paid_select .text-right {
  padding-left: 0;
}
#list-view .thumbnails.events .caption-title a,
#grid-view .thumbnails.events .caption-title a {
  text-decoration: underline;
}
#list-view .thumbnails.events .caption-title {
  margin-bottom: 5px;
}
#list-view .thumbnails.events .caption-category {
  line-height: 22px;
}
#list-view .thumbnails.events .caption-category,
#list-view .thumbnails.events .caption-text,
#list-view .thumbnails.events ul.piped {
  margin-bottom: 10px;
}
#grid-view .thumbnails.events .caption-title,
#grid-view .thumbnails.events .caption-category {
  margin-bottom: 10px;
}
#grid-view .thumbnails.events .media {
  max-height: 250px;
  max-width: 250px;
  margin: 0 auto;
}
