/* Hexagon icon
 * -------------------------------------------------------------------------- */
.rhex {
  background-color: #e06c08;
  margin-top: 14px;
  width: 50px;
  height: 28px;
  border-radius: 2px;
}
.rhex:before,
.rhex:after {
  position: absolute;
  top: 0;
  left: 0;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  background-color: inherit;
  content: '';
}
.rhex:before {
  -webkit-transform: rotate(60deg);
  -moz-transform: rotate(60deg);
  -ms-transform: rotate(60deg);
  -o-transform: rotate(60deg);
  transform: rotate(60deg);
}
.rhex:after {
  -webkit-transform: rotate(-60deg);
  -moz-transform: rotate(-60deg);
  -ms-transform: rotate(-60deg);
  -o-transform: rotate(-60deg);
  transform: rotate(-60deg);
}
/* new rounded hexagon */
.rehex {
  overflow: hidden;
  display: inline-block;
  margin: 4em 0;
  width: 300px;
  /*20em;*/
  height: 260px;
  /*17.32em; /*0.866*/
  transform: rotate(-30deg) skewX(30deg);
  border-radius: 10px;
}
.rehex .rehex-deg {
  display: block;
  overflow: hidden;
  width: inherit;
  height: inherit;
  border-radius: inherit;
}
.rehex .rehex-deg {
  transform: skewX(-30deg) rotate(60deg) skewX(30deg);
}
.rehex .rehex-deg .rehex-deg {
  transform: skewX(-30deg) rotate(60deg) skewX(30deg);
  background: orange;
  /*-webkit-mask-image: -webkit-radial-gradient(circle, white 100%, #000000 100%);*/
  /*-webkit-mask-image: none;*/
  /*-webkit-transform: translateZ(0);*/
}
.rehex .rehex-inner {
  transform: skewX(-30deg) rotate(-90deg) skewX(0deg);
  margin-top: -20px;
  margin-left: 20px;
  width: 260px;
  height: 300px;
}
