/* 2.2 - Logo hexagon icon
/* ========================================================================== */
.logo-hex {
  margin-top: 14px;
  width: 54px;
  height: 31px;
  border-radius: 3px;
  cursor: pointer;
}
.logo-hex:before,
.logo-hex:after {
  position: absolute;
  top: 0;
  left: 0;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  background-color: inherit;
  content: '';
}
.logo-hex:before {
  -webkit-transform: rotate(60deg);
  -moz-transform: rotate(60deg);
  -ms-transform: rotate(60deg);
  -o-transform: rotate(60deg);
  transform: rotate(60deg);
}
.logo-hex:after {
  -webkit-transform: rotate(-60deg);
  -moz-transform: rotate(-60deg);
  -ms-transform: rotate(-60deg);
  -o-transform: rotate(-60deg);
  transform: rotate(-60deg);
}
.shrink .logo-hex {
  margin-top: 7px;
  width: 27px;
  height: 15px;
  border-radius: 1px;
}
