.margin-bottom {
  margin-bottom: 50px;
}
.clear {
  clear: both;
}
.vhidden {
  visibility: hidden;
}
.visible {
  visibility: visible;
}
.overflowed {
  overflow: hidden;
  position: relative;
}
.vertical-align {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
/*[data-animation],*/
.animated {
  visibility: hidden;
  -webkit-animation-duration: .65s;
  animation-duration: .65s;
  /*-webkit-animation-fill-mode: both;
    animation-fill-mode: both;*/
}
.animated .fa {
  visibility: hidden;
}
.animated .animated .fa {
  visibility: hidden;
}
@media (max-width: 991px) {
  .animated,
  .animated .animated,
  .animated .fa,
  .animated .animated .fa {
    /* Disable animation on small devices */
    visibility: visible;
    -webkit-animation-name: animation-off;
    animation-name: animation-off;
  }
}
.vhidden {
  visibility: hidden;
}
.visible {
  visibility: visible;
}
.visible .fa {
  visibility: visible;
}
.visible .visible .fa {
  visibility: visible;
}
